

/***************************/
/* Features Section */
/**************************/
.section__features {
    padding: 9.6rem 6.4rem;
    background-color: #d7f5ec;
    color: #10654c;
    /* font-size: 2rem; */
    font-weight: 500;
  }
  .curve-divider {
    transform: translateY(1.2rem);
  }
  .curve-divider img {
    width: 100%;
  }
  
  .feature__text__box {
    padding: 4.8rem;
    align-self: center;
  }
  
  .heading__secondary {
    font-size: 3.6rem;
    font-weight: 700;
    line-height: 1.05;
    letter-spacing: -0.5px;
    margin-bottom: 1.6rem;
  }
  
  .feature__text {
    font-size: 1.8rem;
    color: #343a40;
    line-height: 1.5;
    margin-bottom: 2.4rem;
  }
  
  .feature__img__box {
    border-radius: 0.8rem;
    overflow: hidden;
    gap: 1.2rem;
  }
  
  .feature__img {
    align-self: start;
    width: 100%;
    transform: scale(1.3);
    transition: all 0.4s;
  }
  .boxImg {
    overflow: hidden;
  }
  .feature__img:hover {
    transform: scale(1.35);
  }